*{
    text-decoration: none;
}
.navbar{
    background: rgb(101, 62, 179); font-family: calibri; padding-right: 20px;padding-left: 150px; position: sticky ; top : 0 ;
}
.navdiv{
    display: flex; align-items: center; justify-content: space-between;
}
.logo a{
    font-size: 40px; font-weight: 600; color: rgb(172, 177, 185);
}
li{
    list-style: none; display: inline-block;
}
li a{
    color: white; font-size: 18px; font-weight: bold; margin-right: 25px;
}
button{
    background-color: black; margin-left: 10px; border-radius: 90px; padding: 10px; width: 100px;
}
button a{
    color: white; font-weight: bold; font-size: 15px;
}